import React from "react"
import Navbar from "./Navbar"
import Main from "./Main"
import "./layout.css"

const Layout = ({ children }) => {
  return (
    <div class="container">
      <title>Red Wizard Design</title>
      <meta name="The homepage of a highly motivated software developer"></meta>
      <Navbar></Navbar>
      <Main>{children}</Main>
    </div>
  )
}

export default Layout
